<template>
  <div>
    <Layout>
      <Header :style="{position: 'fixed', width: '100%'}">
        <Menu mode="horizontal" theme="dark" active-name="1">
          <div class="layout-logo"></div>
          <div class="layout-nav">
            <MenuItem name="1">
              <Icon type="ios-navigate"></Icon>
              课程管理
            </MenuItem>
          </div>
        </Menu>
        <a href="javascript:(0)" @click="layout()"
           style="position: absolute;top: 0;right: 20px;z-index: 1000;color: white;">退出</a>
      </Header>

      <Content :style="{margin: '88px 20px 0', background: '#fff', minHeight: '500px', paddingTop: '20px'}">
        <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="200">
          <FormItem label="课程名" prop="courseName">
            <Input v-model="formValidate.courseName" placeholder="请输入课程名"></Input>
          </FormItem>
          <FormItem label="房间号" prop="roomNo">
            <Input v-model="formValidate.roomNo" placeholder="请输入房间号"></Input>
          </FormItem>
          <FormItem label="课程描述" prop="desc">
            <Input v-model="formValidate.desc" placeholder="课程描述"></Input>
          </FormItem>
          <FormItem>
            <Button type="primary" @click="submit()">提交</Button>
            <Button @click="back()" style="margin-left: 8px">返回</Button>
          </FormItem>
        </Form>
      </Content>
    </Layout>
  </div>
</template>

<script>
  import {fetchGet, fetchPost} from "../../api";

  export default {
    name: "User",
    data() {
      return {
        courseId: 0,
        formValidate: {
          courseName: '',
          roomNo: '',
          desc: ''
        },
        ruleValidate: {
          courseName: [
            {required: true, message: '课程名不能为空!', trigger: 'blur'}
          ],
          roomNo: [
            {required: true, message: '房间号不能为空!', trigger: 'blur'},
          ],
          desc: [
            {required: true, message: '描述不能为空!', trigger: 'blur'}
          ]
        }
      }
    },
    mounted() {
      this.courseId = this.$route.query.courseId
      if (this.courseId != null) {
        this.queryCourseInfo(this.courseId)
      }
    },
    methods: {
      queryCourseInfo(data) {
        let obj = {
          courseId: data
        }
        fetchGet('/v1/course/getCourse', obj).then(res => {
          if (res.code === 200) {
            let data = res.data
            this.formValidate = data
            this.formValidate.roleId = data.role.id
          }
        })
      },
      submit() {
        let obj = {}
        obj = this.formValidate
        fetchPost('/v1/course/saveCourse', obj).then(res => {
          if (res.code === 200) {
            let data = res.data
            this.$router.push({path: '/tea/courses'})
          }
        })
      },
      back() {
        this.$router.push({path: '/tea/courses'})
      },
      layout() {
        localStorage.removeItem("token")
        localStorage.removeItem("userInfo")
        this.$router.push({path: '/'})
      }
    }
  }
</script>

<style scoped>
  .layout-logo {
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
  }

  .layout-nav {
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
  }
</style>



