<template>
  <div>
    <Layout>
      <Header :style="{position: 'fixed', width: '100%'}">
        <Menu mode="horizontal" theme="dark" active-name="1">
          <div class="layout-logo"></div>
          <div class="layout-nav">
            <MenuItem name="1">
              <Icon type="ios-navigate"></Icon>
              用户管理
            </MenuItem>
          </div>
        </Menu>
        <a href="javascript:(0)" @click="layout()"
           style="position: absolute;top: 0;right: 20px;z-index: 1000;color: white;">退出</a>
      </Header>

      <Content :style="{margin: '88px 20px 0', background: '#fff', minHeight: '500px', paddingTop: '20px'}">
        <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="200">
          <FormItem label="用户名" prop="username">
            <Input v-model="formValidate.username" placeholder="请输入用户名"></Input>
          </FormItem>
          <FormItem label="昵称" prop="nickname">
            <Input v-model="formValidate.nickname" placeholder="请输入昵称"></Input>
          </FormItem>
          <FormItem label="学号" prop="no">
            <Input v-model="formValidate.no" placeholder="请输入学号"></Input>
          </FormItem>
          <FormItem label="年龄" prop="age">
            <Input v-model="formValidate.age" placeholder="请输入年龄"></Input>
          </FormItem>
          <FormItem label="电话" prop="phone">
            <Input v-model="formValidate.phone" placeholder="请输入电话"></Input>
          </FormItem>
          <FormItem label="性别" prop="gender">
            <RadioGroup v-model="formValidate.gender">
              <Radio :label="1" value="1">男</Radio>
              <Radio :label="2" value="2">女</Radio>
            </RadioGroup>
          </FormItem>
          <FormItem label="角色" prop="roleId">
            <RadioGroup v-model="formValidate.roleId">
              <Radio :label="2" :value="2">教师</Radio>
              <Radio :label="3" :value="3">学生</Radio>
            </RadioGroup>
          </FormItem>
          <FormItem>
            <Button type="primary" @click="submit()">提交</Button>
            <Button @click="back()" style="margin-left: 8px">返回</Button>
          </FormItem>
        </Form>
      </Content>
    </Layout>
  </div>
</template>

<script>
  import {fetchGet, fetchPost} from "../../api";

  export default {
    name: "User",
    data() {
      return {
        userId: 0,
        formValidate: {
          username: '',
          nickname: '',
          no: '',
          age: '',
          phone: '',
          gender: 0,
          roleId: 0
        },
        ruleValidate: {
          username: [
            {required: true, message: '用户名不能为空!', trigger: 'blur'}
          ],
          nickname: [
            {required: true, message: '昵称不能为空!', trigger: 'blur'},
          ],
          no: [
            {required: true, message: '学号不能为空!', trigger: 'blur'}
          ],
          age: [
            {required: true, message: '年龄不能为空!', trigger: 'blur'},
          ],
          phone: [
            {required: true, type: 'phone', message: '手机号不能为空', trigger: 'blur'}
          ],
          gender: [
            {required: true, message: '性别不能为空!', trigger: 'change'}
          ],
          roleId: [
            {required: true, message: '角色不能为空!', trigger: 'change'}
          ],

        }
      }
    },
    mounted() {
      this.userId = this.$route.query.id
      if (this.userId != null) {
        this.queryUserInfo(this.userId)
      }
    },
    methods: {
      queryUserInfo(data) {
        let obj = {
          userId: data
        }
        let formData = new FormData();
        formData.append('userId', data)
        fetchGet('/v1/user/getUser', obj).then(res => {
          if (res.code === 200) {
            let data = res.data
            this.formValidate = data
            this.formValidate.roleId = data.role.id
          }
        })
      },
      submit() {
        let obj = {}
        obj = this.formValidate
        fetchPost('/v1/user/saveUser', obj).then(res => {
          if (res.code === 200) {
            let data = res.data
            this.$router.push({path: '/admin/users'})
          }
        })
      },
      back() {
        this.$router.push({path: '/admin/users'})
      },
      layout() {
        localStorage.removeItem("token")
        localStorage.removeItem("userInfo")
        this.$router.push({path: '/'})
      }
    }
  }
</script>

<style scoped>
  .layout-logo {
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
  }

  .layout-nav {
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
  }
</style>



