import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Users from '@/views/admin/Users'
import User from '@/views/admin/User'
import Courses from '@/views/tea/Courses'
import Course from '@/views/tea/Course'
import Course1 from '@/views/stu/Courses'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/admin/users',
      name: 'Users',
      component: Users,
    },
    {
      path: '/admin/user',
      name: 'User',
      component: User,
    },
    {
      path: '/tea/courses',
      name: 'Courses',
      component: Courses
    },
    {
      path: '/tea/course',
      name: 'Course',
      component: Course
    },
    {
      path: '/stu/course',
      name: 'Course1',
      component: Course1
    }
  ]
})



