<template>
<!--  <div style="width: 100%;height: 100%;margin: 0;padding: 0;">-->
    <div class="login">
      <h2>登录</h2>
      <Form ref="formInline" :model="formInline" :rules="ruleInline">
        <FormItem prop="user">
          <Input type="text" v-model="formInline.user" placeholder="用户名">
            <Icon type="ios-person-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
        <FormItem prop="password">
          <Input type="password" v-model="formInline.password" placeholder="密码">
            <Icon type="ios-lock-outline" slot="prepend"></Icon>
          </Input>
        </FormItem>
        <FormItem>
          <Button type="primary" @click="handleSubmit()" long>登录</Button>
        </FormItem>
      </Form>
    </div>
<!--  </div>-->

</template>

<script>
  import {fetchPost, fetchGet} from "../api/index";

  export default {
    name: "Login",
    data() {
      return {
        formInline: {
          user: '',
          password: ''
        },
        ruleInline: {
          user: [
            {required: true, message: '用户名不能为空!', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '密码不能为空!', trigger: 'blur'},
            {type: 'string', min: 5, message: '密码长度不能小于5!', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      handleSubmit() {
        let obj = {
          username: this.formInline.user,
          password: this.formInline.password
        };
        fetchPost('/v1/user/login', obj).then(res => {
          if (res.code === 200) {
            let data = res.data
            localStorage.setItem("token", data.token)
            localStorage.setItem("userInfo", data)
            localStorage.setItem("userId", data.id)
            if (data.role.roleName === 'admin') {
              this.$router.push({path: '/admin/users'})
            } else if (data.role.roleName === 'tea') {
              this.$router.push({path: '/tea/courses'})
            } else {
              this.$router.push({path: '/stu/course'})
            }
          }
        })
      }
    }
  }
</script>

<style scoped>
  html, body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
  }

  .login {
    width: 384px;
    height: 500px;
    margin: 0 auto;
    margin-top: 150px;
  }
</style>



