<template>
  <div>
    <Layout>
      <Header :style="{position: 'fixed', width: '100%'}">
        <Menu mode="horizontal" theme="dark" active-name="1">
          <div class="layout-logo"></div>
          <div class="layout-nav">
            <MenuItem name="1">
              <Icon type="ios-navigate"></Icon>
              用户管理
            </MenuItem>
          </div>
        </Menu>
        <a href="javascript:(0)" @click="layout()"
           style="position: absolute;top: 0;right: 20px;z-index: 1000;color: white;">退出</a>
      </Header>

      <Content :style="{margin: '88px 20px 0', background: '#fff', minHeight: '500px'}">
        <div style="margin: 10px 0 10px 0; height: 30px">
          <Button type="primary" style="float: right" @click="addUserInfo()">新增</Button>
        </div>
        <Table :data="data" :columns="columns">
          <template slot-scope="{ row }" slot="role">
            <strong v-if="row.role.roleName === 'admin'">管理员</strong>
            <strong v-else-if="row.role.roleName === 'tea'">教师</strong>
            <strong v-else>学生</strong>
          </template>
          <template slot-scope="{ row, index }" slot="action">
            <Button type="primary" size="small" style="margin-right: 5px" @click="edit(row.id)">编辑</Button>
            <Button type="error" size="small" @click="remove(row.id)">删除</Button>
          </template>
        </Table>
        <div style="margin: 10px;overflow: hidden">
          <div style="float: right;">
            <Page :total="totalElements" :current="pageNo" @on-change="changePage"></Page>
          </div>
        </div>
      </Content>
    </Layout>
  </div>
</template>

<script>
  import {fetchGet, fetchPost} from "../../api";

  export default {
    name: "Users",
    data() {
      return {
        data: [],
        columns: [
          {
            title: '姓名',
            key: 'nickname'
          },
          {
            title: '角色',
            slot: 'role'
          },
          {
            title: '学号或工号',
            key: 'no'
          },
          {
            title: '操作',
            slot: 'action',
            width: 150,
            align: 'center'
          }
        ],
        totalElements: 0,
        pageNo: 1
      }
    },
    mounted() {
      this.queryData()
    },
    methods: {
      addUserInfo() {
        this.$router.push({path: '/admin/user'})
      },
      changePage(pageNo) {
        this.pageNo = pageNo
        this.queryData()
      },
      queryData() {
        let obj = {pageNo: this.pageNo, pageSize: 10}
        fetchGet('/v1/user/getUsers', obj).then(res => {
          if (res.code === 200) {
            let data = res.data
            this.data = data.content
            this.totalElements = data.totalElements
            this.pageNo = data.number + 1
            console.log(data)
          }
        })
      },
      remove(data) {
        let formData = new FormData();
        formData.append("userId", data)
        fetchPost('/v1/user/delUser', formData).then(res => {
          if (res.code === 200) {
            console.log(res.data)
          }
        })
      },
      edit(data) {
        this.$router.push({path: '/admin/user', query: {id: data}})
      },
      handleSubmit() {
        let obj = {
          username: this.formInline.user,
          password: this.formInline.password
        };
        fetchPost('/v1/user/login', obj).then(res => {
          if (res.code === 200) {
            let data = res.data
            localStorage.setItem("token", data.token)
            localStorage.setItem("userInfo", data)
            if (data.role.roleName === 'admin') {
              this.$router.push({path: '/admin/users'})
            } else if (data.role.roleName === 'tea') {
              this.$router.push({path: '/tea/course'})
            } else {
              this.$router.push({path: '/stu/course'})
            }
            console.log(res.data)
          }
        })
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.$Message.success('Success!');
          } else {
            this.$Message.error('Fail!');
          }
        })
      },
      layout() {
        localStorage.removeItem("token")
        localStorage.removeItem("userInfo")
        this.$router.push({path: '/'})
      }
    }
  }
</script>

<style scoped>
  .layout-logo {
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
  }

  .layout-nav {
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
  }

</style>



