<template>
  <div>
    <Layout>
      <Header :style="{position: 'fixed', width: '100%'}">
        <Menu mode="horizontal" theme="dark" active-name="1">
          <div class="layout-logo"></div>
          <div class="layout-nav">
            <MenuItem name="1">
              <Icon type="ios-navigate"></Icon>
              课程管理
            </MenuItem>
          </div>
        </Menu>
        <a href="javascript:(0)" @click="layout()"
           style="position: absolute;top: 0;right: 20px;z-index: 1000;color: white;">退出</a>
      </Header>

      <Content :style="{margin: '88px 20px 0', background: '#fff', minHeight: '500px'}">
        <Table :data="data" :columns="columns">
          <template slot-scope="{ row, index }" slot="action">
            <Button type="primary" size="small" style="margin-right: 5px" @click="sub(row.id)" v-show="row.isSub === 0">订阅</Button>
            <Button type="error" size="small" @click="abbrSub(row.id)" v-show="row.isSub === 1">取消订阅</Button>
          </template>
        </Table>
        <div style="margin: 10px;overflow: hidden">
          <div style="float: right;">
            <Page :total="totalElements" :current="pageNo" @on-change="changePage"></Page>
          </div>
        </div>
      </Content>
    </Layout>
  </div>
</template>

<script>
  import {fetchGet, fetchPost} from "../../api";

  export default {
    name: "Course",
    data() {
      return {
        userId: 0,
        data: [],
        columns: [
          {
            title: '课程名',
            key: 'courseName'
          },
          {
            title: '房间号',
            key: 'roomNo'
          },
          {
            title: '课程描述',
            key: 'desc'
          },
          {
            title: '操作',
            slot: 'action',
            width: 150,
            align: 'center'
          }
        ],
        totalElements: 0,
        pageNo: 1
      }
    },
    mounted() {
      this.userId = localStorage.getItem('userId')
      this.queryCourse()
    },
    methods: {
      queryCourse() {
        let obj = {pageNo: this.pageNo, pageSize: 10}
        fetchGet('/v1/course/getCourses', obj).then(res => {
          if (res.code === 200) {
            let data = res.data
            for (obj in data.content) {
              if (data.content[obj].userIds && data.content[obj].userIds.split(',').includes(this.userId)) {
                data.content[obj].isSub = 1
              } else {
                data.content[obj].isSub = 0
              }
            }
            this.data = data.content
            this.totalElements = data.totalElements
            this.pageNo = data.number + 1
          }
        })
      },
      changePage(pageNo) {
        this.pageNo = pageNo
        this.queryData()
      },
      sub(data) {
        let formData = new FormData();
        formData.append("courseId", data)
        fetchPost('/v1/course/subCourses', formData).then(res => {
          if (res.code === 200) {
            this.queryCourse()
          }
        })
      },
      abbrSub(data) {
        let formData = new FormData();
        formData.append("courseId", data)
        fetchPost('/v1/course/abbrSubCourses', formData).then(res => {
          if (res.code === 200) {
            this.queryCourse()
          }
        })
      },
      layout() {
        localStorage.removeItem("token")
        localStorage.removeItem("userInfo")
        this.$router.push({path: '/'})
      }
    }
  }
</script>

<style scoped>
  .layout-logo {
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
  }

  .layout-nav {
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
  }
</style>



